import React, { FC } from 'react';
import {
  boolColumn,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, Rubrica } from '../../../../models/dtos/mfp/rubrica';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const RubricaHomolognetSearchView: FC<SearchViewProps<Rubrica>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    boolColumn({
      dataField: 'valor',
      filter: true,
      sort: true,
      text: 'Valor',
    }),
    boolColumn({
      dataField: 'quantidade',
      filter: true,
      sort: true,
      text: 'Quantidade',
    }),
    boolColumn({
      dataField: 'percentual',
      filter: true,
      sort: true,
      text: 'Percentual',
    }),
    boolColumn({
      dataField: 'vrBaseCalculo',
      filter: true,
      sort: true,
      text: 'Valor de Base Cálculo',
    }),
  ];
  return (
    <WindowComponent title="Configurações de Rubrica">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'RubricaHomolognetEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default RubricaHomolognetSearchView;
